<script>


import {OKXTronProvider} from "@okxconnect/universal-provider";
import {TronWeb} from "tronweb";

export default {
  name: "TronTransactionPanel",
  methods: {

    async tron_signMessage() {
      this.$emit("generateTransactionParams", "Hello Tron")
      let provider = new OKXTronProvider(window.provider)
      let res = await provider.signMessage("tron:mainnet", "Hello Tron")
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })

      const verify = await tronWeb.trx.verifyMessage("0x"+Buffer.from("Hello Tron").toString("hex"),res, provider.getAccount("tron:mainnet").address)
      this.$emit("generateTransactionResult", "verify:" + verify + res, "tron_signMessage")
    },

    async tron_signMessageV2() {
      this.$emit("generateTransactionParams", "Hello Tron")
      let provider = new OKXTronProvider(window.provider)
      let res = await provider.signMessageV2("tron:mainnet", "Hello Tron")
      if (res.code) {
        this.$emit("generateTransactionResult", res, "tron_signMessage")
        return
      }
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      const base58Address = await tronWeb.trx.verifyMessageV2("Hello Tron", res);
      console.log("tron_signMessageV2_base58Address",base58Address)
      console.log("tron_signMessageV2_address",provider.getAccount("tron:mainnet").address)
      const verify = 2 === provider.getAccount("tron:mainnet").address
      this.$emit("generateTransactionResult", "verify:" + verify + res, "tron_signMessage")
    },

    async tron_signTransaction() {
      let provider = new OKXTronProvider(window.provider)
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      let address = provider.getAccount("tron:mainnet").address
      const transaction = await tronWeb.transactionBuilder.sendTrx(address, 1000, address);
      this.$emit("generateTransactionParams", transaction)

      let res = await provider.signTransaction("tron:mainnet", transaction)
      this.$emit("generateTransactionResult", res, "tron_signTransaction")

    },

    async tron_signAndSendTransaction() {
      let provider = new OKXTronProvider(window.provider)
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      let address = provider.getAccount("tron:mainnet").address
      const transaction = await tronWeb.transactionBuilder.sendTrx(address, 1000, address);
      this.$emit("generateTransactionParams", transaction)

      let tx = await provider.signAndSendTransaction("tron:mainnet", transaction)
      this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
    }

  }
}
</script>

<template>
  <el-row :gutter="12" class="button-row">
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signMessage()">tron_signMessage</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signMessageV2()">tron_signMessageV2</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signTransaction()">tron_signTransaction</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signAndSendTransaction()">
        tron_signAndSendTransaction
      </el-button>
    </el-col>
  </el-row>
</template>

